let itemArray = ['所有人可见', '仅自己可见'];
import uniNavBar from "../../../component/index/uni-nav-bar.vue";
import uploadImg from "../../../component/common/upload-img.vue";
import uniPopup from "../../../component/common/uni-popup.vue"

export default {
	components: {
		uniNavBar,
		uploadImg,
		uniPopup
	},
	onLoad() {
		this.$nextTick(() => {
			this.$refs['showpopup'].open()
		})
	},
	data() {
		return {
			titleText: "所有人可见",
			showSaveDialog: true,
			editText: "",
			imgList: []
		}
	},
	methods: {
		//返回键
		handleBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		//标题点击事件
		handleTitleTypeClick() {
			uni.showActionSheet({
				itemList: itemArray,
				success: (res) => {
					this.titleText = itemArray[res.tapIndex]
				}
			});
		},
		//发布按钮
		handlePublish() {
			console.log("发布按钮")
		},
		//选择图片
		handleSelectImg(imgList) {
			this.imgList = imgList
			console.log(imgList)
		},
		// 关闭弹窗
		handleClosePop() {
			this.$nextTick(() => {
				this.$refs['showpopup'].close()
			})
		},
		//提示窗
		handleShowSaveDialog() {
			uni.showModal({
				content: "是否要保存为草稿",
				cancelText: "不保存",
				confirmText: "保存",
				success: (res) => {
					if (res.confirm) {
						console.log("保存")
					} else {
						console.log("不保存")
					}
					this.showSaveDialog = false
					uni.navigateBack({
						delta: 1
					})
				}
			})
		}
	},
	onBackPress() {
		if ((this.editText || this.imgList.length > 0) && this.showSaveDialog) {
			this.handleShowSaveDialog()
			return true;
		}
	}
}
